import { App } from 'vue'
import 'element-plus/dist/index.css'
import {
  ElButton,
  ElAside,
  ElAlert,
  ElForm,
  ElFormItem,
  ElInput,
  ElRadio
} from 'element-plus'

const components = [
  ElButton,
  ElAside,
  ElAlert,
  ElForm,
  ElFormItem,
  ElInput,
  ElRadio
]
export default function (app: App) {
  for (const component of components) {
    app.component(component.name, component)
  }
}
